import { useEffect, useState } from "react";
import axios from "axios";
import PuffLoader from "react-spinners/PuffLoader";

export default function DestinatairesList({ idMsg }) {
  const [loading, setLoadings] = useState([]);

  const [currentPage, setCurrentPage] = useState(1);
  const [data, setData] = useState([]);
  const [last, setLast] = useState("");

  const fetchData = async () => {
    setLoadings(false);
    axios
      .post(
        `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_DESTINATAIRE_LIST}`,
        { idMessage: idMsg },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      )
      .then(
        (response) => (
          setData(response.data.response.data),
          setLast(response.data.response.last_page)
        )
      )
      .finally(() => setLoadings(true))
      .catch((error) => console.error(error));
  };

  useEffect(() => {
    fetchData();
  }, [currentPage]);

  return (
    <>
      {loading ? (
        data.map((item, index) => {
          return (
            <tr key={index}>
              {/* status receved */}
              {item.notify === 1 ? (
                <th
                  style={{
                    color: "white",
                    background: "#557A46",
                    borderBottomColor: "white",
                    padding: ".30rem",
                    width: ".12rem",
                  }}
                  scope="row"
                ></th>
              ) : (
                <th
                  style={{
                    color: "white",
                    background: "#C70039",
                    borderBottomColor: "white",
                    padding: ".55rem",
                    width: ".12rem",
                  }}
                  scope="row"
                ></th>
              )}
              {/* end status receved */}
              <td style={{ color: "white", fontSize:'meduim' }}> {item.destinataire}</td>
            </tr>
          );
        })
      ) : (
        <div
          style={{
            cursor: "pointer",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            // height: "11.1vh",
            // background: "red",
            height: "65vh",
          }}
        >
          <PuffLoader
            color="#fff"
            size={26}
            cssOverride={{}}
            speedMultiplier={2}
          />
        </div>
      )}
    </>
  );
}
