import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import APP_ROUTES from "../../App/appRoute";
import Whatsapp from "./Pricing/Whatsapp";
import Email from "./Pricing/Email";
import Sms from "./Pricing/Sms";
import Modal from "../Modal";

export default function GlobalCoast() {
  const navigate = useNavigate();

  const [show, setShow] = useState(false);
  const [showValidate, setShowValidate] = useState(false);
  const [text, setText] = useState("");
  const [input, setInput] = useState("");
  const [confirmation, setConfirmation] = useState("");
  const [custumMessage, setCustumMessage] = useState("");

  const hundleAmount = (amount) => {
    if (amount === `Oui pour [${formatMillier(input)}]`) {
      setShowValidate(!true);
      document.querySelector(".submit").click();
      setInput("");
    }
  };

  const formatMillier = (nombre) => {
    nombre += "";
    var sep = " ";
    var reg = /(\d+)(\d{3})/;
    while (reg.test(nombre)) {
      nombre = nombre.replace(reg, "$1" + sep + "$2");
    }
    return nombre;
  };

  const hundleClose = async () => {
    setShow(false);
    setShowValidate(false);
  };

  return (
    <div className="card-body">
      <h5
        className="mb-3"
        style={{ color: "#05004E", fontWeight: "bold", fontSize: "1.1rem" }}
      >
        Cot glabal par service
      </h5>
      <div style={{ display: "flex", flexDirection: "column", gap: "2.2rem" }}>
        <Sms formatMillier={formatMillier} />
        <Whatsapp formatMillier={formatMillier} />
        <Email formatMillier={formatMillier} />
      </div>
    </div>
  );
}
