import axios from "axios";
import { Icon } from "@iconify/react";
import { useEffect, useState } from "react";
import PuffLoader from "react-spinners/PuffLoader";

export default function Modal({
  text,
  message,
  color,
  hundleAmount,
  hundleValidate,
  hundleClose,
  checkId,
  logo,
  none,
  typeMessage,
  confirmation,
  bgColor,
  spinner,
  refresh,

  setbgColorScene,

  rejet,
  rdrtH,
  spinnerWait,
  transaction,
  refreshSolde,
}) {
  const [counter, setCounter] = useState(0);

  const closing = () => {
    const __finish = document.querySelector("#clstrnqx");
    __finish.click();
  };

  // useEffect(() => {
  //   let intervalId;
  //   let timeoutId;

  //   const fetchData = async () => {
  //     // setLoad(false);

  //     axios
  //       .post(
  //         `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_CALLBACK}`,
  //         {
  //           reference: transaction,
  //         },
  //         {
  //           headers: {
  //             Authorization: `Bearer ${localStorage.getItem("token")}`,
  //           },
  //         }
  //       )
  //       .then(
  //         (response) =>
  //           response.data["final_status"] === 200 &&
  //           (clearTimeout(timeoutId), closing(), refreshSolde())
  //       )
  //       // .finally(() => setLoadings(true))
  //       .catch((error) => console.error(error));
  //   };

  //   if (
  //     message ===
  //     "Veuillez dverrouiller votre tlphone et saisir le code pin"
  //   ) {
  //     intervalId = setInterval(() => {
  //       setCounter((prevCounter) => prevCounter + 1);
  //       fetchData();
  //     }, 4000);

  //     timeoutId = setTimeout(() => {
  //       clearInterval(intervalId);
  //       closing();
  //       refreshSolde();
  //     }, 30000);
  //   }

  //   return () => {
  //     clearInterval(intervalId);
  //     clearTimeout(timeoutId);
  //   };
  // }, [message]);


  useEffect(() => {
    let intervalId;let timeoutId;
    let source = axios.CancelToken.source();
  
    const fetchData = async () => {
      try {
        const response = await axios.post(
          `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_CALLBACK}`,
          {
            reference: transaction,
          },
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
            cancelToken: source.token,
          }
        );
  
        if (response.data["final_status"] === 200) {
          clearTimeout(timeoutId);
          closing();
          refreshSolde();
        }
      } catch (error) {
        if (!axios.isCancel(error)) {
          console.error(error);
        }
      }
    };
  
    if (
      message ===
      "Veuillez dverrouiller votre tlphone et saisir le code pin"
    ) {
      intervalId = setInterval(() => {
        setCounter((prevCounter) => prevCounter + 1);
        fetchData();
      }, 4000);
  
      timeoutId = setTimeout(() => {
        clearInterval(intervalId);
        closing();
        refreshSolde();
      }, 45000);
    }
  
    return () => {
      clearInterval(intervalId);
      clearTimeout(timeoutId);
      source.cancel("Request canceled");
    };
  }, [message], transaction, closing, refreshSolde);
  

  return (
    <div
      id="modal"
      className="card-body pseudo_glass"
      style={{
        zIndex: "15",
        top: "50%",
        left: "50%",
        transform: "translate(-50%, -50%)",
        background: "#1b1e21c4",
        width: "100%",
        height: "100vh",
        boxShadow: " 0px 10px 15px -3px rgba(0,0,0,0.1)",
        position: "fixed",
        paddingTop: "0",
      }}
    >
      <div
        id="modal"
        className="card-body"
        style={{
          top: "50%",
          left: "50%",
          transform: "translate(-50%, -50%)",
          width: "35rem",
          height: "40vh",
          borderRadius: "7px",
          boxShadow: " 0px 10px 15px -3px rgba(0,0,0,0.1)",
          position: "fixed",
          zIndex: "9",
          background: bgColor === "" ? "white" : bgColor,
        }}
      >
        {spinner !== "true" ? (
          // || spinner === undefined

          <div
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              alignItems: "center",
              height: "100%",
            }}
          >
            {confirmation === undefined ? (
              <>
                {logo !== undefined ? (
                  <div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "center",
                      height: "50px",
                      // background: "white",
                      objectFit: "cover",
                    }}
                  >
                    <PuffLoader
                      color="#000"
                      cssOverride={{}}
                      speedMultiplier={2}
                      size={30}
                    />
                  </div>
                ) : (
                  <Icon
                    icon="circum:circle-info"
                    color={bgColor !== "" && "whitesmoke"}
                    height="50px"
                  />
                )}
              </>
            ) : null}
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                marginBottom: "30px",
              }}
            >
              {typeMessage}
            </div>

            <div
              style={{
                display: "flex",
                justifyContent: "center",
                marginBottom: "30px",
                color: bgColor !== "" && "whitesmoke",
                // color: bgColor === "" ? "white" : "black",
                //`${bgColor !== "" && "whitesmoke"}
              }}
            >
              {message}
            </div>

            {rejet !== "" && <span style={{ color: "#fff" }}>{rejet}</span>}

            {confirmation !== "" ? (
              <div
                style={{
                  display: "flex",
                  justifyContent: "center",
                  marginBottom: "30px",
                }}
              >
                {confirmation}
              </div>
            ) : null}

            <div
              className="mt-2"
              style={{
                display: "flex",
                justifyContent: "center",
              }}
            >
              {confirmation === undefined ? (
                <div
                  id="kco"
                  className="btn btn-primary me-2"
                  style={{
                    background: color,
                    borderColor: color,
                    display: none,
                  }}
                  onClick={() =>
                    message === "Service Indisponible !"
                      ? hundleClose()
                      : hundleValidate(checkId)
                  }
                >
                  {/* Valider */}
                  {text}
                </div>
              ) : text === null ? null : (
                <div
                  className="btn btn-primary me-2"
                  style={{
                    background: color,
                    // borderColor: `${color !== "" && "whitesmoke"}`,
                    // borderColor: "red",
                    display: none,
                  }}
                  onClick={() => hundleAmount(text)}
                >
                  {text}
                </div>
              )}

              <button
                id="clstrnqx"
                type="reset"
                className="btn btn-outline-secondary"
                onClick={() =>
                  message === "Votre campagne a t lance avec succs"
                    ? (hundleClose(), rdrtH())
                    : refresh
                    ? (setbgColorScene(""), hundleClose(), refresh.click())
                    : hundleClose()
                }
                style={{
                  background: `${bgColor !== "" && "#ef2222f2"}`,
                  borderColor: `${bgColor !== "" && "whitesmoke"}`,
                  color: `${bgColor !== "" && "whitesmoke"}`,
                }}
              >
                Fermer
              </button>
            </div>
          </div>
        ) : (
          <div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              height: "100%",
            }}
          >
            <PuffLoader
              color="#000"
              size={45}
              cssOverride={{}}
              speedMultiplier={2}
            />
          </div>
        )}
      </div>
    </div>
  );
}
