export default function Whatsapp({ enable, service }) {
  const validate = () => {
    enable();
    service("whatsapp");
  };

  return (
    <div>
      <span
        className="badge me-1 isSend"
        style={{ cursor: "pointer", fontWeight: "bold" }}
        onClick={validate}
      >
        Dsactiver La maintenace
      </span>
    </div>
  );
}
