import { useEffect } from "react";
import { useNavigate, useOutletContext } from "react-router-dom";
import APP_ROUTES from "../App/appRoute";
import axios from "axios";
import { useState } from "react";
import Spinner from "./Spinner";
import Pagination from "./Pagination";
import Ext from "./Ext";
import ViewDetail from "./ViewDetail";

export default function Search() {
  const [search] = useOutletContext();
  const navigate = useNavigate();
  // const dir_path = useLocation().pathname.split("/")[2];
  // alert(dir_path);
  // dir_path !== "search" ? setSearch("") : '';

  // console.log(ckText);

  // console.log(user);

  // const tete = (idSelected) => {
  //   return (foundUser = Object.values(data).find((id) => id.id == idSelected));
  // };

  // console.log(tete(search));

  // const [dataClone, setDataClone] = useState([]);
  // const [searchText, setSearchText] = useState("");
  // const [canal, setCanal] = useState("");
  // const [userId, setUserId] = useState("");
  // const [keyword, setKeyword] = useState("");

  const [loading, setLoadings] = useState([]);
  const [data, setData] = useState([]);

  const [show, setShow] = useState(false);
  const [message, setMessage] = useState("");
  const [canal, setCanal] = useState("");
  const [title, setTitle] = useState("");
  const [destinataires, setDestinataires] = useState("");
  const [name, setName] = useState("");

  const fetchData = async (tokeney) => {
    setLoadings(false);

    tokeney = localStorage.getItem("token");
    const url = `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_SEARCH_MESSAGES}`;

    return (
      axios
        .post(`${url}`, {
          token: tokeney,
          keyword: search,
          // keyword: "bako",
        })

        .then((response) => setData(response.data.response))
        // .then((response) => console.log(response.data.response))
        .finally(() => setLoadings(true))
        .catch((error) => console.error(error))
    );
  };

  useEffect(() => {
    if (search !== "") {
      fetchData();
    }
  }, [search]);

  const [currentPage, setCurrentPage] = useState(1);
  const [recordsPerPage, setRecordsPerPage] = useState(10);

  const indexOfLastRecord = currentPage * recordsPerPage;
  const indexOfFirstRecord = indexOfLastRecord - recordsPerPage;
  const currentRecords = data.slice(indexOfFirstRecord, indexOfLastRecord);
  const nPages = Math.ceil(data.length / recordsPerPage);

  const hundleDetail = (name, canal, title, message, destinataires) => {
    setShow(true);

    setName(name);
    setCanal(canal);
    setTitle(title);
    setMessage(message);
    setDestinataires(destinataires);
  };

  const hundleClose = async () => {
    setShow(false);
  };
  return (
    <>
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
        }}
      ></div>
      <hr className="my-0" />
      {!loading ? (
        <Spinner />
      ) : (
        <>
          <div className="card-body">
            <div className="table-responsive text-nowrap">
              <table className="table">
                <thead>
                  <tr>
                    <th>NOM</th>
                    <th>DESTINATAIRES</th>
                    <th>CANAL</th>
                    <th>MESSAGES</th>
                    <th>DATE</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody className="table-border-bottom-0">
                  {Array.isArray(currentRecords)
                    ? Object.values(currentRecords).map((item, index) => {
                        return (
                          <>
                            <tr key={index}>
                              <td>
                                <i className="fab fa-angular fa-lg text-danger"></i>
                                <strong> {item.name} </strong>
                              </td>
                              <td>
                                <i className="fab fa-angular fa-lg text-danger"></i>
                                <strong> {item.destinataires} </strong>
                              </td>
                              <td
                                style={{
                                  maxWidth: "150px",
                                  overflow: "hidden",
                                }}
                              >
                                {item.canal}
                              </td>
                              <td
                                style={{
                                  maxWidth: "150px",
                                  overflow: "hidden",
                                }}
                              >
                                {/* {item.message} */}
                                <Ext message={item.message} />
                              </td>
                              <td>
                                <span>
                                  {new Date(item.created_at).getUTCDate()}/
                                  {(new Date(item.created_at).getUTCMonth() + 1)
                                    .toString()
                                    .padStart(2, "0")}
                                  /{new Date(item.created_at).getUTCFullYear()}
                                </span>
                              </td>
                              <td>
                                <div
                                  style={{
                                    display: "flex",
                                    alignItems: "center",
                                    background: "white",
                                    gap: "13px",
                                    maxWidth: "fit-content",
                                    padding: "0 4px 0 4px",
                                  }}
                                >
                                  <div
                                    style={{
                                      height: "33px",
                                      cursor: "pointer",
                                    }}
                                    onClick={() =>
                                      hundleDetail(
                                        // item.id,
                                        item.name,
                                        item.canal,
                                        item.title,
                                        item.message,
                                        item.destinataires
                                      )
                                    }
                                  >
                                    <img
                                      src={
                                        process.env.PUBLIC_URL +
                                        `/images/iconsicon/share_1.png`
                                      }
                                      style={{
                                        width: "100%",
                                        height: "100%",
                                        objectFit: "cover",
                                      }}
                                      alt="eye"
                                    />
                                  </div>
                                </div>
                              </td>
                            </tr>
                          </>
                        );
                      })
                    : null}
                </tbody>
              </table>
              <>
                <Pagination
                  currentRecords={currentRecords}
                  currentPage={currentPage}
                  setCurrentPage={setCurrentPage}
                  nPages={nPages}
                />
              </>
            </div>
          </div>
          {show && (
            <ViewDetail
              hundleClose={hundleClose}
              name={name}
              canal={canal}
              title={title}
              message={message}
              destinataires={destinataires}
            />
          )}
        </>
      )}
    </>
  );
}
