import { Link } from "react-router-dom";
import APP_ROUTES from "../../../../App/appRoute";
import { useState } from "react";
import axios from "axios";
import { useEffect } from "react";

import PuffLoader from "react-spinners/PuffLoader";
import { Icon } from "@iconify/react";

export default function Sms() {
  const [load, setLoad] = useState(true);
  const [message, setMessage] = useState("");

  const [status, setStatus] = useState();
  const getStatus = async () => {
    setLoad(false);

    axios
      .get(`${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_GETSTATUS_SMS}`, {
        method: "GET",
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      })
      .then((response) => setStatus(response.data.sms_status))
      .finally(() => setLoad(true))
      .catch((error) => console.error(error));
  };

  useEffect(() => {
    getStatus();
  }, []);

  const fetchData = async () => {
    setLoad(false);

    axios
      .post(
        `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_GETMESSAGES_BYCANAL}`,
        { canal: "sms" },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      )
      .then((response) => setMessage(response.data.response.total))
      .finally(() => setLoad(true))
      .catch((error) => console.error(error));
  };

  useEffect(() => {
    fetchData();
  }, []);
  return (
    <div
      // className=" col-md-3 w-24 before-Glass"
      className={`col-md-3 w-24 ${!status ? "before-Glass" : ""}`}
      style={{
        textTransform: "uppercase",
        // height: "22vh",
        height: "fit-content",
        borderRadius: "7px",
        background: "#fff4de",
        position: "relative",
      }}
    >
      <div>
        <div className="text-lowercase mt-3" style={{ display: "flex" }}>
          <div
            style={{
              background: "#ff947a",
              width: "31px",
              height: "31px",
              borderRadius: "50px",
              position: "relative",
            }}
          >
            <div
              style={{
                cursor: "pointer",
                height: "23px",
                position: "absolute",
                top: "50%",
                left: "50%",
                transform: "translate(-50%,-50%)",
              }}
            >
              <img
                src={
                  process.env.PUBLIC_URL + `/images/iconsicon/Order_Pins.png`
                }
                style={{
                  height: "16px",
                  objectFit: "cover",
                }}
                alt="pins"
              />
            </div>
          </div>

          <h4
            className="text-capitalize text-bold"
            style={{ marginLeft: ".9rem", marginTop: ".25rem" }}
          >
            SMS
          </h4>
        </div>
        <div
          className="text-lowercase"
          style={{
            fontWeight: "bold",
          }}
        >
          {load ? (
            <>
              {status === "accept" ? (
                <>
                  {message !== "" ? `${message} envoys ` : "0 envoys"}
                  <span
                    style={{
                      width: "100%",
                      display: "block",
                      marginTop: ".2rem",
                    }}
                  >
                    {message === null ? `${message} envoys ` : "0 checs"}
                  </span>
                  <Link
                    to={APP_ROUTES.sendsms}
                    style={{ visibility: "hidden" }}
                  >
                    <div
                      className="text-lowercase mt-2 "
                      style={{ color: "#4079ED", fontWeight: "bold" }}
                    >
                      Voir plus
                    </div>
                  </Link>
                </>
              ) : (
                <div className="py-3">
                  <div
                    className="text-uppercase mt-3"
                    style={{ visibility: "hidden" }}
                  >
                    Dsactiv
                  </div>
                  <div
                    className="text-uppercase mt-1"
                    style={{ clear: "both" }}
                  ></div>
                </div>
              )}
            </>
          ) : (
            <div
              style={{
                cursor: "pointer",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                height: "10vh",
              }}
            >
              <div>
                <PuffLoader
                  color="#000"
                  size={26}
                  cssOverride={{}}
                  speedMultiplier={2}
                />
                <div
                  className="text-uppercase mt-1"
                  style={{ clear: "both" }}
                ></div>
              </div>
            </div>
          )}
        </div>
      </div>
      <div className="mb-3" style={{ clear: "both" }}></div>
    </div>
  );
}
