import React, { useEffect, useState } from "react";
import Modal from "./Modal";
import { Outlet, useLocation, useNavigate } from "react-router-dom";
import Statistics from "./Client/StatisticsCard/Statistics";
import { Icon } from "@iconify/react";

import axios from "axios";

function Layout({
  show,
  setShow,
  text,
  message,
  color,

  hundleLogout,

  Toggle,
}) {
  const [search, setSearch] = useState("");
  const [isLogo, setIsLogo] = useState("");
  const [load, setLoad] = useState(true);

  // const [showMenu, setShowMenu] = useState(false);
  // const [admin, setAdmin] = useState(false);

  const navigate = useNavigate();
  const dir_path = useLocation().pathname.split("/")[2];
  const canal_path = useLocation().pathname.split("/")[3];

  let path = "";

  if (dir_path !== undefined) {
    switch (dir_path) {
      case "bo":
        path = "dashboard";
        document.title = path.toUpperCase();
        break;
      case "canal":
        path = canal_path;
        document.title = path.toUpperCase();
        break;
      case "money":
        path = "journal";
        document.title = path.toUpperCase();
        break;
      case "carnet":
        path = "contact";
        document.title = path.toUpperCase();
        break;
      case "setting":
        path = "parametres";
        document.title = path.toUpperCase();
        break;
      default:
        // console.log(`Sorry`);
        path = "";
    }
  }

  const hundleClose = async () => {
    setShow(false);
  };

  const hundleValidate = (checkId) => {
    setShow(false);

    if (message === "Se dconnecter ?") {
      navigate("/logout");
    } else {
      alert("Erreur dtecter");
    }
  };

  // const ToggleProfil = () => {
  //   setShowMenu((prevState) => !prevState);
  // };

  // const cleanerToggle = () => {
  //   setShowMenu(false);
  // };

  const getLogo = async () => {
    setLoad(false);

    axios
      .get(`${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_GETLOGO}`, {
        method: "GET",
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      })
      .then((response) => setIsLogo(response.data.lien))
      .finally(() => setLoad(!false))
      .catch((error) => console.log(""));
  };
  useEffect(() => {
    getLogo();
  }, [isLogo]);

  return (
    <>
      <div className="layout-page">
        <nav
          className="layout-navbar container-xxl navbar navbar-expand-xl navbar-detached align-items-center bg-navbar-theme"
          id="layout-navbar"
        >
          <div
            onClick={Toggle}
            className="layout-menu-toggle navbar-nav align-items-xl-center me-3 me-xl-0 d-xl-none"
          >
            <div className="nav-item nav-link px-0 me-xl-4">
              <div
                style={{
                  height: "26px",
                  cursor: "pointer",
                }}
              >
                <img
                  src={process.env.PUBLIC_URL + `/images/menu.png`}
                  style={{
                    width: "100%",
                    height: "100%",
                    objectFit: "cover",
                  }}
                  alt="menu"
                />
              </div>
            </div>
          </div>

          <div
            className=""
            style={{
              width: "100%",
              height: "4rem",
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
            }}
          >
            <input
              type="text"
              className="form-control border-0 shadow-none"
              aria-label="Recherche de message..."
              value={path}
              disabled
              style={{
                background: "transparent",
                textTransform: "uppercase",
                color: "#151D48",
                fontWeight: "bold",
                fontSize: "1.5rem",
                maxWidth: "fit-content",
              }}
            />

            <div className="" style={{ display: "flex", alignItems: "center" }}>
              {!true && (
                <div
                  className=""
                  style={{
                    width: "4rem",
                    height: "1.46rem",
                    borderRadius: ".975rem",
                    display: "flex",
                    alignItems: "center",
                  }}
                >
                  <Icon
                    icon="fontisto:bell"
                    width="100%"
                    height="100%"
                    color="#FF8900"
                  />
                </div>
              )}

              <div
                onClick={() => getLogo()}
                className="me-3 signature"
                style={{
                  // width: "4rem",
                  maxWidth: "7.6rem",
                  height: "3.3rem",
                  borderRadius: ".975rem",
                  display: "flex",
                  alignItems: "center",
                }}
              >
                {isLogo !== null ? (
                  <div
                    style={{
                      height: "2.75rem",
                    }}
                  >
                    <img
                      style={{
                        borderRadius: "7px",
                        maxWidth: "95%",
                        objectFit: "scale-down",
                      }}
                      alt="logo"
                      height="100%"
                      src={isLogo}
                    />
                  </div>
                ) : (
                  <Icon
                    icon="carbon:user-avatar-filled-alt"
                    width="100%"
                    height="100%"
                    color="#000"
                  />
                )}
              </div>

              <div
                className=""
                style={{ height: "4rem", width: "55px" }}
                title={localStorage.getItem("user")}
              >
                <span
                  className="d-block mt-2 fw-semibold text-truncate"
                  style={{ maxWidth: "100px", fontSize: ".85rem" }}
                >
                  {localStorage.getItem("user")}
                </span>
              </div>
            </div>
          </div>
        </nav>

        <div className="container-xxl container-p-y ">
          <div className="row">
            <div className="col-md-12 over-flow">
              <div className="card mb-3">
                <Statistics />
                {/* {admin === false && <Statistics />} */}
              </div>
            </div>
          </div>
        </div>
        <div
          className="content-wrapper"
          // onClick={cleanerToggle}
        >
          <div className="container-xxl flex-grow-1 ">
            <div className="row">
              <div className="col-md-12 over-flow">
                <div className="card mb-3">
                  <Outlet context={[search, setSearch, hundleLogout]} />
                  {/* <Outlet
                    context={[search, setSearch, ckText, searchInputData]}
                  /> */}
                </div>

                {show && (
                  <Modal
                    text={text}
                    message={message}
                    color={color}
                    hundleClose={hundleClose}
                    hundleValidate={hundleValidate}
                    bgColor={""}
                    // logo={"question.jpg"}
                  />
                )}
              </div>
            </div>
          </div>
          <footer className="content-footer footer bg-footer-theme">
            <div className="container-xxl d-flex flex-wrap justify-content-between py-2 flex-md-row flex-column"></div>
          </footer>
          <div className="content-backdrop fade"></div>
        </div>
      </div>
    </>
  );
}

export default Layout;
