import React from "react";
import { useState } from "react";
import { useEffect } from "react";
import Spinner from "../Spinner";
import axios from "axios";
import { useLocation } from "react-router-dom";
import { Icon } from "@iconify/react";

export default function Otp() {
  const dir_path = useLocation().pathname.split("/")[2];
  const [loading, setLoadings] = useState([]);
  const [code, setCode] = useState("");
  const [expectedOTP, setExpectedOTP] = useState();
  const [errorCount, setErrorCount] = useState(0);
  const maxErreurCount = 3;
  const _exError = [];

  const handleChange = (e) => {
    const { value } = e.target;
    setCode(value);
  };

  const isOTPValid = () => {
    return code === expectedOTP;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setLoadings(false);

    axios
      .post(
        `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_OTP_VERIFY}`,
        {
          init_token: dir_path,
          altern_key: code,
        }
      )
      .then((response) => {
        if (response.statusText) {
          localStorage.setItem("_euJDval", response.data.data["is_valid"]);
          window.location = `/cr-pass/${response.data.data["id"]}/${
            response.data.data["is_valid"] === "1" ? "on-recovery" : "error"
          } `;
        }
      })
      .finally(() => setLoadings(true))
      .catch((error) => console.error(error));
  };

  useEffect(() => {
    getData();
  }, [dir_path]);

  const getData = () => {
    axios
      .post(
        `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_GETINIT_TOKEN}`,
        {
          init_token: dir_path, 
        }
      )
      .then((response) => {
        if (response.statusText) {
          setExpectedOTP(response.data.data["altern_key"]);
        }
      })
      .finally(() => setLoadings(true))
      .catch(
        (error) =>
          // navigate(`/err-link/${dir_path}`)
          (window.location = `/err-link/${dir_path}/blocked`)
      );
  };


  const [showMenu, setShowMenu] = useState(!false);

  const Toggle_PWD = () => {
    setShowMenu((prevState) => !prevState);
  };

  return (
    <>
      {loading ? (
        <>
          <div className="container-xxl">
            <div className="authentication-wrapper authentication-basic container-p-y login__form">
              <div className="authentication-inner">
                <div className="card">
                  <div className="card-body">
                    <div className="app-brand justify-content-center"></div>
                    <div
                      style={{
                        display: "flex",
                        justifyContent: "center",
                      }}
                    >
                      <div
                        style={{
                          height: "105px",
                        }}
                      >
                        <img
                          src={process.env.PUBLIC_URL + `/images/gifs/pin.png`}
                          style={{
                            width: "100%",
                            height: "100%",
                            objectFit: "cover",
                          }}
                          alt="pin"
                        />
                      </div>
                    </div>

                    <p className="mt-4 mb-4">
                      Indiquer le code de verification pour continuer..
                    </p>

                    <form
                      id="formAuthentication"
                      className="mb-3"
                      onSubmit={handleSubmit}
                    >
                      <div className="mb-3 form-password-toggle">
                        <div className="d-flex justify-content-between">
                          <label className="form-label" htmlFor="code">
                            Code
                          </label>
                          {code.length === 5 ||
                            (code.length === 6 && (
                              <div>
                                {isOTPValid() ? (
                                  <p style={{ color: "green" }}>
                                    Code OTP correct !
                                  </p>
                                ) : (
                                  (_exError.push({ err: 1, exact: Date.now() }),
                                  localStorage.setItem(
                                    "_exError",
                                    JSON.stringify(_exError)
                                  ),
                                  (
                                    <p style={{ color: "red" }}>
                                      OTP incorrect !
                                    </p>
                                  ))
                                )}
                              </div>
                            ))}
                        </div>
                        <div className="input-group input-group-merge">
                          <input
                            required
                            type={showMenu ? "password" : "text"}
                            value={code}
                            onChange={handleChange}
                            maxLength={6}
                            id="code"
                            className="form-control"
                            name="code"
                            placeholder="&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;"
                            aria-describedby="password"
                          />

                          <span
                            className="input-group-text cursor-pointer"
                            style={{
                              backgroundColor: "#5D5FEF",
                            }}
                            onClick={Toggle_PWD}
                          >
                            {showMenu ? (
                              <Icon icon="uil:eye-slash" color="#fff" />
                            ) : (
                              <Icon icon="mingcute:eye-2-line" color="#fff" />
                            )}
                          </span>
                        </div>
                        <div className="password error"></div>
                      </div>
                      <div className="mb-4"></div>
                      <div className="mb-3">
                        {isOTPValid() ? (
                          <button
                            className="btn btn-primary d-grid w-100"
                            type="submit"
                            // disabled={!validate()}
                          >
                            Valider
                          </button>
                        ) : (
                          ""
                        )}
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </>
      ) : (
        <div
          style={{
            height: "100vh",
            background: "#fff",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Spinner />
        </div>
      )}

      <style>
        {` 
          /* width */
          #scroll::-webkit-scrollbar {
              width: 1px;
          }

          /* Track */
          #scroll::-webkit-scrollbar-track {
              background: #f1f1f1;
          }

          /* Handle */
          #scroll::-webkit-scrollbar-thumb {
              background: rgb(133, 132, 132);
          }
        `}
      </style>
    </>
  );
}
