import React, { useState } from "react";
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import APP_ROUTES from "../../App/appRoute";
import PuffLoader from "react-spinners/PuffLoader";

function Recovery() {
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [loading, setLoadings] = useState(false);
  const [ErrMessage, setErrMessage] = useState("");

  const hundleSubmitRegisterForm = async (e) => {
    e.preventDefault();
    setLoadings(true);

    axios
      .post(
        `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_RECOVERY}`,
        {
          email: email,
        }
      )
      .then((response) => {
        if (response.data.status === "succes") {
          navigate(APP_ROUTES.index);
          window.location.reload(false);
        }
        console.log(response.data);
      })
      .finally(() => setLoadings(false))
      // .catch((error) => console.log(""));
      .catch(
        (error) => (
          console.log(""), setErrMessage(error.response.data["status"])
        )
      );
  };

  return (
    <>
      <div className="container-xxl">
        <div className="authentication-wrapper authentication-basic container-p-y login__form">
          <div className="authentication-inner">
            <div className="card">
              <div className="card-body">
                <div className="app-brand justify-content-center"></div>

                <p className="mt-4 mb-4">
                  Entrez votre adresse email pour continuer..
                </p>

                <form id="formAuthentication" className="mb-3">
                  <div className="mb-3">
                    <label htmlFor="email" className="form-label">
                      Email
                    </label>
                    <input
                      type="email"
                      className="form-control"
                      id="email"
                      name="email-username"
                      placeholder="Entrez votre Email"
                      onChange={(e) => setEmail(e.target.value)}
                    />
                    <div className="email error"></div>
                  </div>

                  <div className="mb-4"></div>
                  <div className="mb-3">
                    {loading ? (
                      <div
                        style={{
                          width: "100%",
                          height: ".97rem",
                          cursor: "pointer",
                          display: "flex",
                          alignItems: "center",
                          justifyContent: "center",
                          // border: "red solid 1px",
                        }}
                      >
                        <PuffLoader
                          color="#000"
                          size={25}
                          cssOverride={{}}
                          speedMultiplier={1}
                        />
                      </div>
                    ) : (
                      <button
                        className="btn btn-primary d-grid w-100"
                        type="submit"
                        onClick={hundleSubmitRegisterForm}
                        //   disabled={!validate()}
                      >
                        Rinitialiser
                      </button>
                    )}
                  </div>
                </form>
                <p
                  className="text-center"
                  style={{ display: "flex", justifyContent: "space-between" }}
                >
                  <span>Dja un compte ?</span>
                  <Link to={APP_ROUTES.index}>
                    <span>Se connecter</span>
                  </Link>
                </p>
                {ErrMessage === "error" && (
                  <span
                    className="pt-3"
                    style={{
                      fontSize: "12px",
                      color: "red",
                      display: "flex",
                      alignItems: "center",
                      width: "100%",
                      justifyContent: "center",
                    }}
                  >
                    {"aucun utilisateur trouv"}
                  </span>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Recovery;
