import React from "react";
import { useState } from "react";
import { useEffect } from "react";
import Spinner from "./Spinner";
import axios from "axios";
import APP_ROUTES from "../App/appRoute";
export default function Logout(/*{ sessionToken }*/) {
  const [loading, setLoadings] = useState([]);
  // const navigate = useNavigate();

  const hundleSubmitLogout = (tokeney) => {
    tokeney = localStorage.getItem("token");

    axios
      .get(`${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_LOGOUT}${tokeney}`)
      .then((response) => {
        if (response.statusText) {
          localStorage.clear();
          // navigate("/");
          window.location = APP_ROUTES.index;
        }
      })
      .finally(() => setLoadings(true))
      .catch((error) => console.error(error));
  };

  useEffect(() => {
    hundleSubmitLogout();
  }, []);

  return (
    <>
      {!loading ? (
        <>Logout</>
      ) : (
        <div
          style={{
            height: "100vh",
            background: "#fff",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          {" "}
          <Spinner />
        </div>
      )}
    </>
  );
}
