import { Icon } from "@iconify/react";
import { Link } from "react-router-dom";
import APP_ROUTES from "../../../../App/appRoute";

import axios from "axios";
import { useEffect, useState } from "react";
import PuffLoader from "react-spinners/PuffLoader";
// import BounceLoader from "react-spinners/BounceLoader";

export default function Solde() {
  const [load, setLoad] = useState(true);
  const [message, setMessage] = useState("");
  const fetchData = async () => {
    setLoad(false);

    axios
      .get(`${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_SOLDE}`, {
        method: "GET",
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      })
      .then((response) => setMessage(response.data.solde))
      .finally(() => setLoad(true))
      .catch((error) => console.error(error));
  };

  useEffect(() => {
    fetchData();
  }, []);

  const formatMillier = (nombre) => {
    nombre += "";
    var sep = " ";
    var reg = /(\d+)(\d{3})/;
    while (reg.test(nombre)) {
      nombre = nombre.replace(reg, "$1" + sep + "$2");
    }
    return nombre;
  };

  return (
    <div
      className=" col-md-3 w-24"
      style={{
        textTransform: "uppercase",
        // height: "22vh",
        height: "fit-content",
        borderRadius: "7px",
        background: "#ffe2e5",
      }}
    >
      <div>
        <div className="text-lowercase mt-3" style={{ display: "flex" }}>
          <div
            style={{
              background: "#fa5a7d",
              width: "31px",
              height: "31px",
              borderRadius: "50px",
              position: "relative",
            }}
          >
            <div
              style={{
                cursor: "pointer",
                height: "23px",
                position: "absolute",
                top: "50%",
                left: "50%",
                transform: "translate(-50%,-50%)",
              }}
            >
              <Icon
                // icon="ant-design:signal-filled"
                icon="uis:signal-alt-3"
                color="white"
                height="13px"
              />

              {/* <BounceLoader color="#36d7b7" /> */}
            </div>
          </div>

          <h4
            className="text-capitalize text-bold"
            style={{
              marginLeft: ".9rem",
              marginTop: ".25rem",
            }}
          >
            SOLDE
          </h4>
        </div>
        <div
          className="text-lowercase"
          style={{
            fontWeight: "bold",
          }}
        >
          {load ? (
            <>
              {message === undefined || message === null ? (
                <span className="text-uppercase">0 FCFA</span>
              ) : (
                <span className="text-uppercase">
                  {formatMillier(message)} FCFA
                </span>
              )}

              <Link
                to={APP_ROUTES.money}
                // style={{ visibility: "hidden" }}
              >
                <div
                  className="text-lowercase mt-4 "
                  style={{ color: "#4079ed", fontWeight: "bold" }}
                >
                  Mon activit
                </div>
              </Link>
            </>
          ) : (
            <div
              style={{
                cursor: "pointer",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                height: "11.1vh",
              }}
            >
              <PuffLoader
                color="#000"
                size={26}
                cssOverride={{}}
                speedMultiplier={2}
              />
            </div>
          )}
        </div>
      </div>
      <div className="mb-4" style={{ clear: "both" }}></div>
    </div>
  );
}
