import "react-tabs/style/react-tabs.css";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import Sms from "./Sms";
import Email from "./Email";
import Whatsapp from "./Whatsapp";
import axios from "axios";
import { useEffect, useState } from "react";
import Modal from "../../Modal";

export default function TabsDispatch() {
  const [show, setShow] = useState(false);
  const [bgColorScene, setbgColorScene] = useState("");
  const [custumMessage, setCustumMessage] = useState("");
  const [spinner, setSpinner] = useState("");

  const SOFT_REFRESH = document.querySelector(".freshin");

  const hundleClose = async () => {
    setShow(false);
  };

  const hundleAccept = async (id, canal) => {
    setShow(true);
    setSpinner("true");

    switch (canal) {
      case "sms":
        const hundleSubmitSms = (identity) => {
          axios
            .put(
              `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_ACTIVE_SMS}`,
              {
                userId: identity,
              },
              {
                headers: {
                  Authorization: `Bearer ${localStorage.getItem("token")}`,
                },
              }
            )
            .then((response) => {
              setCustumMessage(response.data.message);
            })
            .finally(() => (setSpinner(""), setbgColorScene("#12dd28f2")))
            .catch(
              (error) =>
                console.error(
                  error
                ) /* (window.location = `/err-link/${dir_path}/blocked`)*/
            );
        };

        hundleSubmitSms(id);
        break;
      case "whatsapp":
        const hundleSubmitWhatsapp = (identity) => {
          axios
            .put(
              `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_ACTIVE_WHATSAPP}`,
              {
                userId: identity,
              },
              {
                headers: {
                  Authorization: `Bearer ${localStorage.getItem("token")}`,
                },
              }
            )
            .then((response) => {
              setCustumMessage(response.data.message);
            })
            .finally(() => (setSpinner(""), setbgColorScene("#12dd28f2")))
            .catch(
              (error) =>
                console.error(
                  error
                ) /* (window.location = `/err-link/${dir_path}/blocked`)*/
            );
        };

        hundleSubmitWhatsapp(id);
        break;
      case "email":
        const hundleSubmitEmail = (identity) => {
          axios
            .put(
              `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_ACTIVE_EMAIL}`,
              {
                userId: identity,
              },
              {
                headers: {
                  Authorization: `Bearer ${localStorage.getItem("token")}`,
                },
              }
            )
            .then((response) => {
              setCustumMessage(response.data.message);
            })
            .finally(() => (setSpinner(""), setbgColorScene("#12dd28f2")))
            .catch(
              (error) =>
                console.error(
                  error
                ) /* (window.location = `/err-link/${dir_path}/blocked`)*/
            );
        };

        hundleSubmitEmail(id);
        break;
      default:
        return null;
    }
  };

  const hundleRejet = async (id, canal) => {
    setShow(true);
    setSpinner("true");

    switch (canal) {
      case "sms":
        const hundleSubmitSms = (identity) => {
          axios
            .put(
              `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_REJECT_SMS}`,
              {
                userId: identity,
              },
              {
                headers: {
                  Authorization: `Bearer ${localStorage.getItem("token")}`,
                },
              }
            )
            .then((response) => {
              setCustumMessage(response.data.message);
            })
            .finally(() => (setSpinner(""), setbgColorScene("#ef2222f2")))
            .catch(
              (error) =>
                console.error(
                  error
                ) /* (window.location = `/err-link/${dir_path}/blocked`)*/
            );
        };

        hundleSubmitSms(id);
        break;
      case "whatsapp":
        const hundleSubmitWhatsapp = (identity) => {
          axios
            .put(
              `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_REJECT_WHATSAPP}`,
              {
                userId: identity,
              },
              {
                headers: {
                  Authorization: `Bearer ${localStorage.getItem("token")}`,
                },
              }
            )
            .then((response) => {
              setCustumMessage(response.data.message);
            })
            .finally(() => (setSpinner(""), setbgColorScene("#ef2222f2")))
            .catch(
              (error) =>
                console.error(
                  error
                ) /* (window.location = `/err-link/${dir_path}/blocked`)*/
            );
        };

        hundleSubmitWhatsapp(id);
        break;
      case "email":
        const hundleSubmitEmail = (identity) => {
          axios
            .put(
              `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_REJECT_EMAIL}`,
              {
                userId: identity,
              },
              {
                headers: {
                  Authorization: `Bearer ${localStorage.getItem("token")}`,
                },
              }
            )
            .then((response) => {
              setCustumMessage(response.data.message);
            })
            .finally(() => (setSpinner(""), setbgColorScene("#ef2222f2")))
            .catch(
              (error) =>
                console.error(
                  error
                ) /* (window.location = `/err-link/${dir_path}/blocked`)*/
            );
        };

        hundleSubmitEmail(id);

        break;
      default:
        return null;
    }
  };

  return (
    <>
      <Tabs>
        <TabList>
          <Tab>DEMANDE SMS </Tab>
          <Tab>DEMANDE EMAIL</Tab>
          <Tab>DEMANDE WHATSAPP</Tab>
        </TabList>
        <TabPanel>
          <Sms hundleAccept={hundleAccept} hundleRejet={hundleRejet} />
        </TabPanel>
        <TabPanel>
          <Email hundleAccept={hundleAccept} hundleRejet={hundleRejet} />
        </TabPanel>
        <TabPanel>
          <Whatsapp hundleAccept={hundleAccept} hundleRejet={hundleRejet} />
        </TabPanel>
      </Tabs>

      {show && (
        <Modal
          text={"text"}
          message={custumMessage}
          color={"red"}
          hundleClose={hundleClose}
          none={"none"}
          bgColor={bgColorScene}
          spinner={spinner}
          refresh={SOFT_REFRESH}
          // render white bgrand #fff
          setbgColorScene={setbgColorScene}
        />
      )}
    </>
  );
}
