import { useEffect, useState } from "react";
import axios from "axios";

import Modal from "../Modal";
import Spinner from "../Spinner";
import SmsDisable from "./Maintenance/Disable/Sms";
import SmsEnable from "./Maintenance/Enable/Sms";

import EmailDisable from "./Maintenance/Disable/Email";
import EmailEnable from "./Maintenance/Enable/Email";

import WhatsappDisable from "./Maintenance/Disable/Whatsapp";
import WhatsappEnable from "./Maintenance/Enable/Whatsapp";

export default function Maintenance() {
  const dir_path = "maintenace";

  document.title = dir_path.toUpperCase();

  const [loading, setLoadings] = useState([]);

  const [show, setShow] = useState(false);
  const [message, setMessage] = useState("");
  const [text, setText] = useState("");
  const [canal, setCanal] = useState("");
  const [color, setColor] = useState("");
  const [checkId, setCheckId] = useState();
  const [statusSms, setStatusSms] = useState();
  const [statusEmail, setStatusEmail] = useState();
  const [statusWhatsapp, setStatusWhatsapp] = useState();
  const [messageConsumer, setMessageConsumer] = useState("");

  const fetchDataSmsService = async () => {
    axios
      .get(
        `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_GETSTATUS_SERVICE_SMS}`,
        {
          method: "GET",
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      )
      .then((response) => setStatusSms(response.data.maintenance))
      .catch((error) => console.error(error));
  };

  const fetchDataEmailService = async () => {
    axios
      .get(
        `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_GETSTATUS_SERVICE_EMAIL}`,
        {
          method: "GET",
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      )
      .then((response) =>  setStatusEmail(response.data.maintenance)
      )
      .catch((error) => console.error(error));
  };

  const fetchDataWhatsappService = async () => {
    axios
      .get(
        `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_GETSTATUS_SERVICE_WHATSAPP}`,
        {
          method: "GET",
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      )
      .then((response) => setStatusWhatsapp(response.data.maintenance))
      .catch((error) => console.error(error));
  };

  useEffect(() => {
    fetchDataSmsService();
    fetchDataEmailService();
    fetchDataWhatsappService();
  }, []);

  const hundleClose = async () => {
    setShow(false);
  };

  const hundleDisableOff = async (service, message) => {
    setShow(true);
    message = "Confirmer la dsactivation du service ?";
    setMessage(`${message}`);
    setColor("#ea1c1cc9");
    setText("Dsactiver");
    setMessageConsumer(message);
    // setCanal(service);
  };

  const hundleDisableOn = async (service, message) => {
    setShow(true);
    message = "Confirmer l'activation du service ?";
    setMessage(`${message}`);
    setColor("#109f04cc");
    setText("Activer");
    setMessageConsumer(message);
    // setCanal(service);
  };

  const hundleValidate = () => {
    // setShow(false);

    switch (canal) {
      case "email":
        if (messageConsumer === "Confirmer l'activation du service ?") {
          const fetchData = async () => {
            axios
              .put(
                `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_ENABLE_SERVICE_EMAIL}`,
                {
                  id: "",
                },
                {
                  headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`,
                  },
                }
              )
              .then((response) => fetchDataEmailService())
              .catch((error) => console.error(error));
          };

          fetchData();
          setShow(false);
        } else if (message === "Confirmer la dsactivation du service ?") {
          const fetchData = async () => {
            axios
              .put(
                `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_DISABLE_SERVICE_EMAIL}`,
                {
                  id: "",
                },
                {
                  headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`,
                  },
                }
              )
              .then((response) => fetchDataEmailService())
              .catch((error) => console.error(error));
          };

          fetchData();
          setShow(false);
        } else {
          alert("Erreur dtecter");
        }
        break;
      case "whatsapp":
        if (messageConsumer === "Confirmer l'activation du service ?") {
          const fetchData = async () => {
            axios
              .put(
                `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_ENABLE_SERVICE_WHATSAPP}`,
                {
                  id: "",
                },
                {
                  headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`,
                  },
                }
              )
              .then((response) => fetchDataWhatsappService())
              .catch((error) => console.error(error));
          };

          fetchData();
          setShow(false);
        } else if (message === "Confirmer la dsactivation du service ?") {
          const fetchData = async () => {
            axios
              .put(
                `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_DISABLE_SERVICE_WHATSAPP}`,
                {
                  id: "",
                },
                {
                  headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`,
                  },
                }
              )
              .then((response) => fetchDataWhatsappService())
              .catch((error) => console.error(error));
          };

          fetchData();
          setShow(false);
        } else {
          alert("Erreur dtecter");
        }

        break;
      case "sms":
        if (messageConsumer === "Confirmer l'activation du service ?") {
          const fetchData = async () => {
            axios
              .put(
                `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_ENABLE_SERVICE_SMS}`,
                {
                  id: "",
                },
                {
                  headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`,
                  },
                }
              )
              .then((response) => fetchDataSmsService())
              .catch((error) => console.error(error));
          };
          fetchData();
          setShow(false);
        } else if (message === "Confirmer la dsactivation du service ?") {
          const fetchData = async () => {
            axios
              .put(
                `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_DISABLE_SERVICE_SMS}`,
                {
                  id: "",
                },
                {
                  headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`,
                  },
                }
              )
              .then((response) => fetchDataSmsService())
              .catch((error) => console.error(error));
          };
          fetchData();
          setShow(false);
        } else {
          alert("Erreur dtecter");
        }
        break;
      default:
      // console.log(`Sorry`);
    }
  };

  return (
    <>
      {!loading ? (
        <Spinner />
      ) : (
        <>
          <div className="card-body">
            <h5
              className="mb-3"
              style={{
                color: "#05004E",
                fontWeight: "bold",
                fontSize: "1.1rem",
              }}
            >
              Mode Maintenance
            </h5>
            <div className="table-responsive text-nowrap">
              <table className="table">
                <thead>
                  <tr>
                    <th>NID</th>
                    <th>Services</th>
                    <th> Action</th>
                  </tr>
                </thead>
                <tbody className="table-border-bottom-0">
                  <tr>
                    <td>
                      <i className="fab fa-angular fa-lg text-danger"></i> 1
                    </td>
                    <td>Sms</td>
                    <td
                      style={{
                        display: "flex",
                        alignItems: "center",
                      }}
                    >
                      {!statusSms ? (
                        <SmsEnable
                          enable={hundleDisableOn}
                          service={setCanal}
                        />
                      ) : (
                        <SmsDisable
                          disable={hundleDisableOff}
                          service={setCanal}
                        />
                      )}
                    </td>
                  </tr>

                  <tr>
                    <td>
                      <i className="fab fa-angular fa-lg text-danger"></i> 2
                    </td>
                    <td>Email</td>
                    <td
                      style={{
                        display: "flex",
                        alignItems: "center",
                      }}
                    >
                      {!statusEmail ? (
                        <EmailEnable
                          enable={hundleDisableOn}
                          service={setCanal}
                        />
                      ) : (
                        <EmailDisable
                          disable={hundleDisableOff}
                          service={setCanal}
                        />
                      )}
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <i className="fab fa-angular fa-lg text-danger"></i> 3
                    </td>
                    <td>Whatsapp</td>
                    <td
                      style={{
                        display: "flex",
                        alignItems: "center",
                      }}
                    >
                      {!statusWhatsapp ? (
                        <WhatsappEnable
                          enable={hundleDisableOn}
                          service={setCanal}

                          // WhatsappService={fetchDataWhatsappService}
                        />
                      ) : (
                        <WhatsappDisable
                          disable={hundleDisableOff}
                          service={setCanal}
                        />
                      )}
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>

          {show && (
            <Modal
              text={text}
              message={message}
              color={color}
              hundleValidate={hundleValidate}
              hundleClose={hundleClose}
              checkId={checkId}
              bgColor={""}
              // logo={"question.jpg"}
            />
          )}
        </>
      )}
    </>
  );
}
