import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import Spinner from "./Spinner";
import axios from "axios";
import Ext from "./Ext";
import ViewDetail from "./ViewDetail";
import CardMessageBoxe from "./Tables/CardMessageBoxe";
import UltimePager from "./UltimePager";

export default function MessageBoxe() {
  const dir_path = useLocation().pathname.split("/")[2];

  document.title = dir_path.toUpperCase();

  const [show, setShow] = useState(false);
  const [message, setMessage] = useState("");
  const [canal, setCanal] = useState("");
  const [title, setTitle] = useState("");
  const [destinataires, setDestinataires] = useState("");
  const [name, setName] = useState("");
  const [msgId, setMsgId] = useState("");

  const [currentPage, setCurrentPage] = useState(1);
  const [recordsPerPage, setRecordsPerPage] = useState(10);

  const [loading, setLoadings] = useState([]);
  const [data, setData] = useState([]);
  const [last, setLast] = useState("");

  const indexOfLastRecord = currentPage * recordsPerPage;
  const indexOfFirstRecord = indexOfLastRecord - recordsPerPage;

  const fetchData = async () => {
    setLoadings(false);

    axios
      .get(`${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_ADMIN_GET_ALLMESSAGES}`, {
        method: "GET",
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
        params: { perPage: "9", page: currentPage },
      })
      .then(
        (response) => (
          setData(response.data.response.data),
          setLast(response.data.response.last_page)
        )
      )
      .finally(() => setLoadings(true))
      .catch((error) => console.error(error));
  };

  useEffect(() => {
    fetchData();
  }, [currentPage, setData]);

  const handlePageChange = (page) => {
    setCurrentPage(page);
  };

  const hundleClose = async () => {
    setShow(false);
  };

  const hundleDetail = (id, name, canal, title, message, destinataires) => {
    setShow(true);

    setMsgId(id);
    setName(name);
    setCanal(canal);
    setTitle(title);
    setMessage(message);
    setDestinataires(destinataires);
  };

  const removeTags = (str) => {
    if (str === null || str === "") return false;
    else str = str.toString();
    return str.replace(/(<([^>]+)>)/gi, "");
  };
  // console.log(removeTags('<html>Welcome jordane.</html>'));;

  return (
    <>
      {!loading ? (
        <Spinner />
      ) : (
        <>
          <div className="card-body">
            <h5
              className="mb-3"
              style={{
                color: "#05004E",
                fontWeight: "bold",
                fontSize: "1.1rem",
              }}
            >
              Messages envoys
            </h5>
            <div className="table-responsive text-nowrap">
              <table className="table">
                <thead>
                  <tr>
                    <th>NOM</th>
                    <th>DESTINATAIRES</th>
                    <th>CANAL</th>
                    <th>MESSAGES</th>
                    <th>TITRE</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody className="table-border-bottom-0">
                  <>
                    {data.map((item, index) => {
                      return (
                        <CardMessageBoxe
                          item={item}
                          Ext={Ext}
                          hundleDetail={hundleDetail}
                          removeTags={removeTags}
                          key={index}
                        />
                      );
                    })}
                  </>
                </tbody>
              </table>
              <>
                {last > 1 ? (
                  <UltimePager
                    total={last}
                    currentPage={currentPage}
                    onPageChange={handlePageChange}
                  />
                ) : null}
              </>
            </div>
          </div>
          {/**/}
          {show && (
            <ViewDetail
              hundleClose={hundleClose}
              msgId={msgId}
              name={name}
              canal={canal}
              title={title}
              message={message}
              destinataires={destinataires}
            />
          )}
        </>
      )}
    </>
  );
}

{
  /* <td>
                                  <span>
                                    {new Date(item.created_at).getUTCDate()}/
                                    {(
                                      new Date(item.created_at).getUTCMonth() +
                                      1
                                    )
                                      .toString()
                                      .padStart(2, "0")}  
                                    /
                                    {new Date(item.created_at).getUTCFullYear()}
                                  </span>
                                </td> */
}
