import { useEffect, useState } from "react";
import EmptyMessage from "../../EmptyMessage";
import Spinner from "../../Spinner";
import CardDemandeSms from "../../Tables/CardDemandeSms";
import UltimePager from "../../UltimePager";
import axios from "axios";
import { RefreshPage } from "../../RefreshPage";

export default function Sms({ hundleAccept, hundleRejet }) {
  const [datasms, setDatasms] = useState([]);
  const [last, setLast] = useState("");

  const [currentPage, setCurrentPage] = useState(1);

  const handlePageChange = (page) => {
    setCurrentPage(page);
  };

  const [loading, setLoadings] = useState([]);

  const fetchData = async () => {
    setLoadings(false);

    axios
      .get(
        `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_GET_REQUESTS}`,
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      )
      .then((response) =>
        response.statusText === "OK"
          ? setDatasms(response.data.demande_sms)
          : null
      )
      .finally(() => setLoadings(true))
      .catch((error) => console.error(error));
  };

  useEffect(() => {
    fetchData();
  }, []);
  return (
    <>
      <RefreshPage fetchData={fetchData} />
      {!loading ? (
        <Spinner />
      ) : (
        <div className="table-responsive text-nowrap">
          <table className="table">
            <thead>
              <tr>
                <th>NID</th>
                <th>Code Sms</th>
                <th>STATUT VALIDATION</th>
                <th
                  style={{
                    display: "flex",
                    justifyContent: "center",
                  }}
                >
                  Actions
                </th>
              </tr>
            </thead>
            {/* <tbody className="table-border-bottom-0"><>poop</></tbody> */}
            <tbody className="table-border-bottom-0">
              {datasms?.map((item, index) => {
                return (
                  <CardDemandeSms
                    hundleAccept={hundleAccept}
                    hundleRejet={hundleRejet}
                    item={item}
                    count={index + 1}
                  />
                );
              })}
            </tbody>
          </table>
          {last > 1 ? (
            <UltimePager
              total={last}
              currentPage={currentPage}
              onPageChange={handlePageChange}
            />
          ) : null}
          <>{datasms.length === 0 && <EmptyMessage />}</>
        </div>
      )}
    </>
  );
}
