import { useEffect, useState } from "react";
import PuffLoader from "react-spinners/PuffLoader";
import LinkCompagn from "../Navigate/LinkCompagn";
import axios from "axios";

export default function Campagne({ itemIsActive }) {
  const [load, setLoad] = useState(true);
  const [status, setStatus] = useState();

  const getStattusService = async () => {
    setLoad(false);

    axios
      .get(
        `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_GETSTATUS_SERVICEACTIF}`,
        {
          method: "GET",
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      )
      .then((response) => setStatus(response.data))
      .finally(() => setLoad(!false))
      .catch((error) => console.error(error));
  };
  useEffect(() => {
    getStattusService();
  }, [status]);
  return (
    <>
      {load ? ( 
        <>{status === 1 && <LinkCompagn itemIsActive={itemIsActive} />}</>
      ) : (
        <li className={`menu-item `}>
          <div className="menu-link client">
            <div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <PuffLoader
                color="#000"
                size={15}
                cssOverride={{}}
                speedMultiplier={2}
              />
            </div>
          </div>
        </li>
      )}
    </>
  );
}
