import { useNavigate } from "react-router-dom";
import APP_ROUTES from "../../App/appRoute";
import { useState } from "react";

export default function Protected({ Composant }) {
  const [authentification, setAuthentification] = useState(
    localStorage.getItem("auth")
  );


  const navigate = useNavigate();
  const Oauth = (auth) => {
    return auth;
  };

  return Oauth(authentification) ? (
    <Composant />
  ) : (
    navigate("/", {
      state: { from: window.location.replace(`${APP_ROUTES.index}`) },
    })
    // <Navigate to="/" state={{ from: location }} replace />
  );
}
