import axios from "axios";
import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Spinner from "../Spinner";
import APP_ROUTES from "../../App/appRoute";
import { Icon } from "@iconify/react";
import Modal from "../Modal";

function CreatePass() {
  const navigate = useNavigate();
  const dir_path = useLocation().pathname.split("/")[2];
  const recovery = useLocation().pathname.split("/")[3];

  const [isLogo, setIsLogo] = useState("");
  const [showSucces, setShowSucces] = useState(false);
  const [bgColorScene, setbgColorScene] = useState("");
  const [custumMessage, setCustumMessage] = useState("");

  const [password, setPassword] = useState([]);
  const [password2, setPassword2] = useState([]);
  const [loading, setLoadings] = useState([]);

  useEffect(() => {
    try {
      const statusRecov = localStorage.getItem("_euJDval");
      console.log(statusRecov);

      if (parseInt(statusRecov) === 1) {
        if (toString(recovery) === "on-recovery") {
          console.log("yuces_ucs");
        }
      } else {
        localStorage.removeItem("_euJDval");
        window.location = `/err-link/${dir_path}/blocked`;
      }
    } catch (error) {
      console.log(error);
    }
  }, [dir_path]);

  const hundleSubmit = (e) => {
    e.preventDefault();
    if (password.trim() === password2.trim()) {
      setLoadings(false);

      axios
        .post(
          `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_OTPCREATE_PASS}`,
          {
            id: dir_path,
            password: password,
          },
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        )
        .then((response) => {
          if (response.statusText) {
            navigate(APP_ROUTES.index);
            window.location = APP_ROUTES.index;
            localStorage.removeItem("_euJDval");
          }
        })
        .finally(() => setLoadings(true))
        .catch(
          (error) =>
            /*console.error(error)*/ (window.location = `/err-link/${dir_path}/blocked`)
        );
    } else {
      setShowSucces(true);
      setIsLogo(undefined);
      setCustumMessage("vrifier mot de passe");
      setbgColorScene("#ef2222f2");
    }
  };

  const [showMenu1, setShowMenu1] = useState(!false);

  const Toggle_PWD1 = () => {
    setShowMenu1((prevState) => !prevState);
  };

  const [showMenu2, setShowMenu2] = useState(!false);

  const Toggle_PWD2 = () => {
    setShowMenu2((prevState) => !prevState);
  };

  return (
    <>
      {loading ? (
        <div className="container-xxl">
          <div className="authentication-wrapper authentication-basic container-p-y login__form">
            <div className="authentication-inner">
              <div className="card">
                <div className="card-body">
                  <div className="app-brand justify-content-center"></div>
                  <h5
                    className="mb-3"
                    style={{ fontSize: "1rem", fontWeight: "bold" }}
                  >
                    Nouveau mot de passe <span> </span>
                  </h5>
                  {/* <p className="mb-4">Remplir les champs pour continuer..</p> */}

                  <form
                    id="formAuthentication"
                    className="mb-3"
                    onSubmit={hundleSubmit}
                  >
                    <div className="mb-3 form-password-toggle">
                      <div className="d-flex justify-content-between">
                        <label className="form-label" htmlFor="password1">
                          mot de passe
                        </label>
                      </div>
                      <div className="input-group input-group-merge">
                        <input
                          required
                          type={showMenu1 ? "password" : "text"}
                          id="password1"
                          name="password1"
                          className="form-control"
                          aria-describedby="password"
                          placeholder="&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;"
                          onChange={(e) => setPassword(e.target.value)}
                        />

                        <span
                          className="input-group-text cursor-pointer"
                          style={{
                            backgroundColor: "#5D5FEF",
                          }}
                          onClick={Toggle_PWD1}
                        >
                          {showMenu1 ? (
                            <Icon icon="uil:eye-slash" color="#fff" />
                          ) : (
                            <Icon icon="mingcute:eye-2-line" color="#fff" />
                          )}
                        </span>
                      </div>
                      <div className="password error"></div>
                    </div>

                    <div className="mb-3 form-password-toggle">
                      <div className="d-flex justify-content-between">
                        <label className="form-label" htmlFor="password2">
                          Confirmez mot de passe
                        </label>
                      </div>
                      <div className="input-group input-group-merge">
                        <input
                          required
                          type={showMenu2 ? "password" : "text"}
                          id="password2"
                          name="password2"
                          className="form-control"
                          aria-describedby="password"
                          placeholder="&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;"
                          onChange={(e) => setPassword2(e.target.value)}
                        />

                        <span
                          className="input-group-text cursor-pointer"
                          style={{
                            backgroundColor: "#5D5FEF",
                          }}
                          onClick={Toggle_PWD2}
                        >
                          {showMenu2 ? (
                            <Icon icon="uil:eye-slash" color="#fff" />
                          ) : (
                            <Icon icon="mingcute:eye-2-line" color="#fff" />
                          )}
                        </span>
                      </div>
                      <div className="password error"></div>
                    </div>

                    <div className="mb-4"></div>
                    <div className="mb-3">
                      <button
                        className="btn btn-primary d-grid w-100"
                        type="submit"
                      >
                        Valider
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>

          {showSucces && (
            <Modal
              text={"text"}
              message={custumMessage}
              hundleClose={() => setShowSucces(false)}
              none={"none"}
              bgColor={bgColorScene}
              logo={isLogo}
            />
          )}
        </div>
      ) : (
        <div
          style={{
            height: "100vh",
            background: "#fff",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Spinner />
        </div>
      )}
    </>
  );
}

export default CreatePass;
