import axios from "axios";

import { useEffect, useState } from "react";
import PuffLoader from "react-spinners/PuffLoader";

export default function ResumeWhatsapp() {
  const [load, setLoad] = useState(true);
  const [message, setMessage] = useState("");
  const fetchData = async () => {
    setLoad(false);

    axios
      .get(`${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_GETMESSAGES_BYCANAL}`, {
        method: "GET",
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
        params: { canal: "whatsapp" },
      })
      .then((response) => setMessage(response.data.response.total))
      .finally(() => setLoad(true))
      .catch((error) => console.error(error));
  };

  useEffect(() => {
    fetchData();
  }, []);
  return (
    <div
      className=" col-md-4"
      style={{
        textTransform: "uppercase",
        height: "22vh",
        borderRadius: "7px",
        background: "#dcfcef",
      }}
    >
      <div>
        <div className="text-lowercase mt-3" style={{ display: "flex" }}>
          <div
            style={{
              background: "#3CD856",
              width: "31px",
              height: "31px",
              borderRadius: "50px",
              position: "relative",
            }}
          >
            <div
              style={{
                cursor: "pointer",
                height: "23px",
                position: "absolute",
                top: "50%",
                left: "50%",
                transform: "translate(-50%,-50%)",
              }}
            >
              <img
                src={process.env.PUBLIC_URL + `/images/iconsicon/Disc_Icon.png`}
                style={{
                  width: "100%",
                  height: "100%",
                  objectFit: "cover",
                }}
                alt="disc"
              />
            </div>
          </div>

          <h4
            className="text-capitalize text-bold"
            style={{ marginLeft: ".9rem", marginTop: ".25rem" }}
          >
            RESUME WHATSAPP
          </h4>
        </div>

        <div
          className="text-lowercase"
          style={{
            fontWeight: "bold",
          }}
        >
          {load ? (
            <>
              {/* {message} envoys */}

              {message.length === 0 ? " 0 envoy" : `${message} envoys`}
              <span
                style={{
                  width: "100%",
                  display: "block",
                  marginTop: ".2rem",
                }}
              >
                0 envois en cours
              </span>
              <span
                style={{
                  width: "100%",
                  display: "block",
                  marginTop: ".2rem",
                }}
              >
                0 checs
              </span>
            </>
          ) : (
            <div
              style={{
                cursor: "pointer",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                height: "11.1vh",
              }}
            >
              <PuffLoader
                color="#000"
                size={26}
                cssOverride={{}}
                speedMultiplier={2}
              />
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
