import { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Spinner from "./Spinner";
import Modal from "./Modal";
import axios from "axios";
import APP_ROUTES from "../App/appRoute";
import Pager from "./Pager";
import CardAdminView from "./Tables/CardAdminView";
import UltimePager from "./UltimePager";

export default function AdminList() {
  const navigate = useNavigate();

  const [loading, setLoadings] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [data, setData] = useState([]);
  const [last, setLast] = useState("");

  const [show, setShow] = useState(false);
  const [message, setMessage] = useState("");
  const [text, setText] = useState("");
  const [color, setColor] = useState("");
  const [checkId, setCheckId] = useState();

  useEffect(() => {
    fetchData();
  }, [currentPage]);

  const handlePageChange = (page) => {
    setCurrentPage(page);
  };

  const fetchData = async () => {
    setLoadings(false);

    axios
      .get(`${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_ADMIN_LIST}`, {
        method: "POST",
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
        params: { page: currentPage },
      })
      .then(
        (response) => (
          setData(response.data.agents.data),
          setLast(response.data.agents.last_page)
        )
      )
      .finally(() => setLoadings(true))
      .catch((error) => console.error(error));
  };

  const hundleValidate = (checkId) => {
    setShow(false);

    if (message === "Confirmer l'activation du compte ?") {
      setLoadings(false);

      axios
        .put(
          `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_ON_STATUT}`,
          {
            id: checkId,
          },
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        )
        .then((response) => {
          // console.log(response.data);
        })

        // .finally(() => (setLoadings(true), window.location.reload(false)))
        .finally(() => (setLoadings(true), fetchData()))
        .catch((error) => console.log(error));
    } else if (message === "Confirmer la dsactivation du compte ?") {
      setLoadings(false);

      axios
        .put(
          `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_OFF_STATUT}`,
          {
            id: checkId,
          },
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        )
        .then((response) => {
          // console.log(response.data);
        })
        // .finally(() => (setLoadings(true), window.location.reload(false)))
        .finally(() => (setLoadings(true), fetchData()))
        .catch((error) => console.log(error));
    } else if (message === "Voir l'activit du compte") {
      // window.location = "/dashbaord/clientspace/" + checkId;
      navigate("/clientspace/recap/" + checkId);
      // clientspace/recap/${response.data["user"].id}
    } else if (message === "Se dconnecter ?") {
      navigate("/logout");
    } else {
      alert("Erreur dtecter");
    }
  };

  const hundleClose = async () => {
    setShow(false);
  };

  const hundleChangeStatutOff = async (id, message) => {
    setShow(true);
    message = "Confirmer la dsactivation du compte ?";
    setMessage(`${message}`);
    setColor("#ea1c1cc9");
    setText("Dsactiver");
    setCheckId(id);
  };

  const hundleChangeStatutOn = async (id, message) => {
    setShow(true);
    message = "Confirmer l'activation du compte ?";
    setMessage(`${message}`);
    setColor("#109f04cc");
    setText("Activer");
    setCheckId(id);
  };

  return (
    <>
      {!loading ? (
        <Spinner />
      ) : (
        <>
          <div className="card-body">
            <h5
              className="mb-3"
              style={{
                color: "#05004E",
                fontWeight: "bold",
                fontSize: "1.1rem",
              }}
            >
              Liste Admins
            </h5>
            <div className="table-responsive text-nowrap">
              <table className="table">
                <thead>
                  <tr>
                    <th>ID</th>
                    <th>Nom</th>
                    <th>Email</th>
                    <th>Tlephone</th>
                    <th>Status</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody className="table-border-bottom-0">
                  <>
                    {data.map((item, index) => {
                      return (
                        <CardAdminView
                          item={item}
                          hundleChangeStatutOff={hundleChangeStatutOff}
                          hundleChangeStatutOn={hundleChangeStatutOn}
                          // hundleChangeEdit={hundleChangeEdit}
                          // hundleGotoDashClient={hundleGotoDashClient}
                          key={index}
                        />
                      );
                    })}
                  </>
                </tbody>
              </table>

              <>
                {last > 1 ? (
                  <UltimePager
                    total={last}
                    currentPage={currentPage}
                    onPageChange={handlePageChange}
                  />
                ) : null}
              </>
            </div>
          </div>
          {show && (
            <Modal
              text={text}
              message={message}
              color={color}
              hundleValidate={hundleValidate}
              hundleClose={hundleClose}
              checkId={checkId}
              bgColor={""}
              // logo={"question.jpg"}
            />
          )}
        </>
      )}
    </>
  );
}
