export default function Whatsapp({ disable, service }) {
  const validate = () => {
    disable();
    service("whatsapp");
  };

  return (
    <div>
      <span
        // className="badge me-1 isSend"
        className="badge me-1 isInActiveStatut"
        style={{ cursor: "pointer", fontWeight: "bold" }}
        onClick={validate}
      >
        Activer La maintenace
      </span>
    </div>
  );
}
