import React, { useEffect, useState } from "react";
import AsideBar from "./AsideBar";
import Layout from "./Layout";
import Logout from "./Logout";
import { useNavigate } from "react-router-dom";


function Dashboard2() {
  let isOauthAdmin = "2";

  // const dir_path = useLocation().pathname;
  const navigate = useNavigate();

  const [showAsideFirst, setShowAsideFirst] = useState(false);
  const [showAsideSecond, setShowAsideSecond] = useState(false);
  const [showAsideThird, setShowAsideThird] = useState(false);
  const [showAsideFourth, setShowAsideFourth] = useState(false);

  const [show, setShow] = useState(false);
  const [text, setText] = useState("");
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("");
  const [logo, setLogo] = useState("");

  const hundleLogout = async (message) => {
    setShow(true);
    message = "Se dconnecter ?";
    setColor("#5d5fef");
    setText("Oui");
    setMessage(`${message}`);
  };

  const [open, setOpen] = useState(true);

  const Toggle = () => {
    setOpen((prevState) => !prevState);
    const htmlElement = document.documentElement;
    htmlElement.classList.toggle("layout-menu-expanded");
  };

  return (
    <div className="layout-wrapper layout-content-navbar">
      <div className="layout-container">
        <AsideBar
          showAsideFirst={showAsideFirst}
          setShowAsideFirst={setShowAsideFirst}
          showAsideSecond={showAsideSecond}
          setShowAsideSecond={setShowAsideSecond}
          showAsideThird={showAsideThird}
          showAsideFourth={showAsideFourth}
          setShowAsideThird={setShowAsideThird}
          setShowAsideFourth={setShowAsideFourth}
          Logout={Logout}
          hundleLogout={hundleLogout}
          Toggle={Toggle}
          isAdmin = {isOauthAdmin}
        />

        <Layout
          show={show}
          setShow={setShow}
          text={text}
          setText={setText}
          message={message}
          setMessage={setMessage}
          color={color}
          setColor={setColor}
          hundleLogout={hundleLogout}
          Toggle={Toggle}
        />
      </div>

      {/* <div className="layout-overlay layout-menu-toggle"></div> */}
    </div>
  );
}

export default Dashboard2;
