export default function Sms({ enable, service }) {
  const validate = () => {
    enable();
    service("sms");
  };
  return (
    <div>
      <span
        className="badge me-1 isSend"
        style={{ cursor: "pointer", fontWeight: "bold" }}
        onClick={validate}
      >
        Dsactiver La maintenace
      </span>
    </div>
  );
}
