import { useNavigate } from "react-router-dom";
import { useState } from "react";
import Spinner from "../Spinner";
import axios from "axios";
import { Icon } from "@iconify/react";
import APP_ROUTES from "../../App/appRoute";

export default function Login() {
  document.title = "Login";
  const navigate = useNavigate();

  const [loading, setLoadings] = useState([]);

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [ErrMessage, setErrMessage] = useState("");

  const hundleSubmitLoginForm = async (e) => {
    e.preventDefault();

    setLoadings(false);

    axios
      .post(
        `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_CONNEXION}`,
        {
          email: email.trim(),
          password: password,
        }
      )
      .then((response) =>
        response.data["status"] === "echec"
          ? navigate(APP_ROUTES.disableaccount)
          : response.data["status"] === "success" &&
            response.data["user"].role_id === 2 &&
            response.data["user"].status === 1
          ? (localStorage.setItem("auth", true),
            localStorage.setItem("isUser", response.data["user"].role_id),
            localStorage.setItem("token", response.data["token"]),
            navigate(APP_ROUTES.view))
          : // : response.data.status === "echec"
            // ? navigate(APP_ROUTES.disableaccount)
            ""
      )
      .finally(() => setLoadings(true))
      .catch(
        (error) => (
          console.log(""), setErrMessage(error.response.data["status"])
        )
      );

    // response.data["user"].status !== 1 ||
    // .catch((error) => console.error(error));
  };

  const validate = () => {
    return email.length > 0 && password.length >= 4;
  };
  const [showMenu, setShowMenu] = useState(!false);

  const Toggle_PWD = () => {
    setShowMenu((prevState) => !prevState);
  };

  return (
    <>
      {loading ? (
        <div className="container-xxl">
          <div className="authentication-wrapper authentication-basic container-p-y login__form">
            <div className="authentication-inner">
              <div className="card">
                <div className="card-body">
                  <div className="app-brand justify-content-center">
                    <a href="index.html" className="app-brand-link gap-2"></a>
                  </div>
                  <h4
                    className="mb-2 fw-lighter"
                    style={{
                      fontSize: "1.3rem",
                    }}
                  >
                    Salut ! <span> </span>
                  </h4>
                  <p className="mb-4">
                    Veuillez vous identifier pour continuer..
                  </p>

                  <form
                    id="formAuthentication"
                    className="mb-3"
                    onSubmit={hundleSubmitLoginForm}
                  >
                    <div className="mb-3">
                      <label htmlFor="email" className="form-label">
                        Email
                      </label>
                      <input
                        type="email"
                        className="form-control"
                        id="email"
                        name="email-username"
                        placeholder="Entrez votre Email"
                        onChange={(e) => setEmail(e.target.value)}
                      />
                      <div className="email error"></div>
                    </div>

                    <div className="mb-3 form-password-toggle">
                      <div className="d-flex justify-content-between">
                        <label className="form-label" htmlFor="password">
                          mot de passe
                        </label>
                      </div>
                      <div className="input-group input-group-merge">
                        <input
                          required
                          type={showMenu ? "password" : "text"}
                          id="password"
                          className="form-control"
                          name="password"
                          placeholder="&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;"
                          aria-describedby="password"
                          onChange={(e) => setPassword(e.target.value)}
                        />

                        <span
                          className="input-group-text cursor-pointer"
                          style={{
                            backgroundColor: "#5D5FEF",
                          }}
                          onClick={Toggle_PWD}
                        >
                          {showMenu ? (
                            <Icon icon="uil:eye-slash" color="#fff" />
                          ) : (
                            <Icon icon="mingcute:eye-2-line" color="#fff" />
                          )}
                        </span>
                      </div>
                      <div className="password error"></div>
                    </div>
                    <div className="mb-4"></div>
                    <div className="mb-3">
                      <button
                        className="btn btn-primary d-grid w-100"
                        type="submit"
                        disabled={!validate()}
                      >
                        Connexion
                      </button>
                    </div>
                  </form>

                  <p
                    className="text-center"
                    style={{
                      display: "flex",
                      justifyContent: "space-between",
                    }}
                  >
                    {!true && (
                      <>
                        <span>Pas de compte ?</span>
                        <a href={`${APP_ROUTES.newAccount}`}>
                          <span>Crer un compte </span>
                        </a>
                      </>
                    )}
                  </p>

                  <div
                    style={{ display: "flex", justifyContent: "space-between" }}
                  >
                    {!true && (
                      <>
                        <span>Mots de passe perdu ?</span>
                        <a href="/recovery-account/">
                          <span>J'ai oubli</span>
                        </a>
                      </>
                    )}
                  </div>

                  {ErrMessage === "error" && (
                    <span
                      className="pt-3"
                      style={{
                        fontSize: "12px",
                        color: "red",
                        display: "flex",
                        alignItems: "center",
                        width: "100%",
                        justifyContent: "center",
                      }}
                    >
                      {"identifiants incorrect"}
                    </span>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      ) : (
        <div
          style={{
            height: "100vh",
            background: "#fff",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          {" "}
          <Spinner />
        </div>
      )}
    </>
  );
}
