import { useEffect, useState } from "react";
import axios from "axios";
import { Icon } from "@iconify/react";

import PuffLoader from "react-spinners/PuffLoader";

export default function MinePrice() {
  const [load, setLoad] = useState(true);
  const [loading, setLoadings] = useState([]);

  const [message, setMessage] = useState("");
  const [input, setInput] = useState("");

  const hundleChangeMontant = (e) => {
    const value = e.target.value;
    if (/^\d*$/.test(value)) {
      setInput(value);
    } else {
      document.querySelector(".mine_price").value = "";
    }
  };

  const fetchData = async () => {
    setLoad(false);

    axios
      .get(
        `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_MINE_PRICE}`,
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      )
      .then((response) => setMessage(response.data))
      .finally(() => setLoad(true))
      .catch((error) => console.error(error));
  };

  const hundleSubmit = () => {
    setLoadings(false);

    axios
      .put(
        `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_MINE_PRICE}`,
        {
          mine_price: input,
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      )
      .then((response) => {})
      .finally(
        () => (
          setInput(""),
          setMessage(""),
          (document.querySelector(".mine_price").value = ""),
          fetchData(),
          setLoadings(true)
        )
      )
      .catch(
        (error) =>
          console.error(
            error
          ) /* (window.location = `/err-link/${dir_path}/blocked`)*/
      );
  };

  useEffect(() => {
    fetchData();
  }, []);
  return (
    <div
      style={{
        background: "#33333324",
        padding: "1rem",
        borderRadius: "6px",
      }}
    >
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignContent: "center",
        }}
      >
        <span
          style={{
            fontSize: "1.1rem",
            color: "black",
            fontWeight: "bold",
            marginLeft: "1.5rem",
            cursor: "pointer",
          }}
        >
          RECHARGE MINIMUM
        </span>
      </div>
      <div style={{ display: "flex", gap: "1.8rem" }} className="mt-3">
        <input
          className="mine_price"
          placeholder={message}
          type="text"
          onChange={hundleChangeMontant}
          style={{
            background: "#FAFBFC",
            width: "100%",
            height: "2.3rem",
            borderRadius: "4px",
            paddingLeft: "1rem",
          }}
        />

        {!loading ? (
          <div
            style={{
              cursor: "pointer",
              display: "flex",
              alignItems: "center",
            }}
          >
            <div>
              <PuffLoader
                color="#000"
                size={26}
                cssOverride={{}}
                speedMultiplier={2}
              />
              <div
                className="text-uppercase mt-1"
                style={{ clear: "both" }}
              ></div>
            </div>
          </div>
        ) : (
          <>
            {input.length > 2 && (
              <div style={{ cursor: "pointer" }} onClick={() => hundleSubmit()}>
                <Icon
                  icon="majesticons:edit-pen-2"
                  width="100%"
                  height="30px"
                  color="#5d5fef"
                />
              </div>
            )}
          </>
        )}
      </div>

      {/* <button type="submit" style={{ display: "none" }} className="submit">
  Submit
</button> */}
      <button
        style={{ display: "none" }}
        type="reset"
        className="btn btn-outline-secondary reset-btn"
      >
        Reset
      </button>
    </div>
  );
}
