export default function Sms({ disable, service }) {
  const validate = () => {
    disable();
    service("sms");
  };
  return (
    <div>
      <span
        className="badge me-1 isInActiveStatut"
        style={{ cursor: "pointer", fontWeight: "bold" }}
        onClick={validate}
      >
        Activer La maintenace
      </span>
    </div>
  );
}
