export default function Email({ enable, service }) {
  const validate = () => {
    enable();
    service("email");
  };

  return (
    <div>
      <span
        // className="badge me-1 isSend"
        className="badge me-1 isSend"
        style={{ cursor: "pointer", fontWeight: "bold" }}
        onClick={validate}
      >
        Dsactiver La maintenace
      </span>
    </div>
  );
}
