import axios from "axios";
import { useEffect, useState } from "react";
import { Icon } from "@iconify/react";
import Modal from "../../Modal";

export default function Whatsapp({ formatMillier }) {
  const [load, setLoad] = useState(true);
  const [message, setMessage] = useState("");
  const [confirmation, setConfirmation] = useState("");
  const [custumMessage, setCustumMessage] = useState("");

  const [show, setShow] = useState(false);
  const [showValidate, setShowValidate] = useState(false);
  const [disabled, setDisabled] = useState(!false);
  const [text, setText] = useState("");
  const [input, setInput] = useState("");
  const [loading, setLoadings] = useState([]);

  // id grille
  const gridID = 1;

  const hundleClose = async () => {
    setShow(false);
    setShowValidate(false);
  };

  const hundleAmount = (amount) => {
    if (amount === `Oui pour [${formatMillier(input)}]`) {
      setShowValidate(!true);
      hundleSubmitWHATSAPP();
    }
  };

  const hundleValidate = async () => {
    if (input === "" || input === undefined) {
      setShow(true);
      setCustumMessage(`Tarif WHATSAPP incorrect`);
    } else {
      setShowValidate(true);
      setText(`Oui pour [${formatMillier(input)}]`);
      setCustumMessage(
        `Le montant actuel est de [${formatMillier(message)}] !`
      );
      setConfirmation(`confirmez le nouveau tarif ? `);
    }
  };

  const hundleSubmitWHATSAPP = () => {
    setLoadings(false);

    axios
      .put(
        `${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_TRANSACTION_WHATSAPP}`,
        {
          amount: input,
          id: gridID,
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      )
      .then((response) => {
        if (
          response.data.status === "success" &&
          response.data.nouveau_prix === input
        ) {
          setShow(true);
          setDisabled(false);
          setCustumMessage(`Tarif WHATSAPP mis  jour`);
          fetchData();
          document.querySelector(".input_value-WHATSAPP").value = "";
          setInput("");
        }
      })
      .finally(() => setLoadings(true))
      .catch(
        (error) =>
          console.error(
            error
          ) /* (window.location = `/err-link/${dir_path}/blocked`)*/
      );
  };

  const fetchData = async () => {
    setLoad(false);

    axios
      .get(`${process.env.REACT_APP_BASEURL}${process.env.REACT_APP_TRANSACTION_WHATSAPP}/${gridID}`, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      })
      .then((response) => setMessage(response.data))
      .finally(() => setLoad(true))
      .catch((error) => console.error(error));
  };

  useEffect(() => {
    fetchData();
  }, []);

  return (
    <>
      {!false && (
        <div
          style={{
            background: "#DCFCE7",
            padding: "1rem",
            borderRadius: "6px",
          }}
        >
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              alignContent: "center",
            }}
          >
            <div
              style={{
                background: "#3CD856",
                width: "31px",
                height: "31px",
                borderRadius: "50px",
                position: "relative",
              }}
            >
              <div
                style={{
                  cursor: "pointer",
                  height: "23px",
                  position: "absolute",
                  top: "50%",
                  left: "50%",
                  transform: "translate(-50%,-50%)",
                }}
              >
                <img
                  src={
                    process.env.PUBLIC_URL + `/images/iconsicon/Disc_Icon.png`
                  }
                  style={{
                    width: "100%",
                    height: "100%",
                    objectFit: "cover",
                  }}
                  alt="Disc_Icon"
                />
              </div>
            </div>

            <span
              style={{
                fontSize: "1.1rem",
                color: "black",
                fontWeight: "bold",
                marginLeft: "1.5rem",
                cursor: "pointer",
              }}
            >
              WHATSAPP
            </span>
          </div>
          <div style={{ display: "flex", gap: "1.8rem" }} className="mt-3">
            <input
              className="input_value-WHATSAPP"
              placeholder={message}
              type="text"
              pattern="[0-9]{0,5}"
              onChange={(e) => setInput(e.target.value)}
              style={{
                background: "#FAFBFC",
                width: "100%",
                height: "2.3rem",
                borderRadius: "4px",
                paddingLeft: "1rem",
              }}
            />

            <div style={{ cursor: "pointer" }} onClick={() => hundleValidate()}>
              <Icon
                icon="majesticons:edit-pen-2"
                width="100%"
                height="30px"
                color="#5d5fef"
              />
            </div>
          </div>

          <button type="submit" style={{ display: "none" }} className="submit">
            Submit
          </button>
          <button
            style={{ display: "none" }}
            type="reset"
            className="btn btn-outline-secondary reset-btn"
          >
            Reset
          </button>
        </div>
      )}
      {show && (
        <Modal
          text={text}
          message={custumMessage}
          hundleClose={hundleClose}
          none={"none"}
          bgColor={""}
        />
      )}
      {showValidate && (
        <Modal
          text={text}
          message={custumMessage}
          confirmation={confirmation}
          hundleClose={hundleClose}
          // logo={"info.jpg"}
          hundleAmount={hundleAmount}
          bgColor={""}
        />
      )}
    </>
  );
}
